.scroll {
    width: 90%;
    height: 500px;
    overflow: auto;
    float: left;
    padding: 10px 20px 10px 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    text-align: justify;
  }

.scroll4::-webkit-scrollbar {
    width: 10px;
  }
   
  .scroll4::-webkit-scrollbar-thumb {
    background: #666;
    border-radius: 20px;
  }
  
  .scroll4::-webkit-scrollbar-track {
    background: #ddd;
    border-radius: 20px;
  }